:root {
    --primary: #ddd;
    --dark: #333;
    --light: rgb(239, 239, 239);
    --shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
    --alarm: #a64452;
}

html{
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    color: var(--dark);
}

body {
    background: #ccc;
    margin: 30px 50px;
    line-height: 1.4;
}

input {
    background-color: var(--light);
    width: 3rem;
}

.btn {
    background: var(--dark);
    color: var(--light);
    padding: 0.6rem 1.3rem;
    text-decoration: none;
    border: 0;
}

.btn:hover {
    background: var(--light);
    color: var(--dark);
    cursor: pointer;
}

.btn:disabled {
    background: grey;
    color: rgb(153, 153, 153);
    pointer-events: none;
}

.homeheader {
    font-size: 6rem;
    padding: 0;
    margin: 8rem 0 0 0;
    text-align: center;
    text-shadow: var(--shadow);
}

/* Navigation */

.navbar {
    margin: 20px 20px 32px 20px;
}

.navbar ul {
    display: grid;
    grid-gap: 20px;
    padding: 0;
    list-style: none;
    grid-template-columns: repeat(3, 1fr);
}

.navlink {
    background: var(--primary);
    display: block;
    text-decoration: none;
    padding: 0.8rem;
    text-align: center;
    color: var(--dark);
    font-size: 1.6rem;
    box-shadow: var(--shadow);
}

.navlink:hover {
    background: var(--dark);
    color: var(--light);
}

/* Timer */

.timer{
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
    background-color: var(--primary);
    box-shadow: var(--shadow);
    margin: auto;
    width: 50%;
}

.timer h1 {
    margin: 0;
    padding: 0 2.2rem;
    justify-self: start;
    font-size: 1rem;
}

.timer p {
    margin: 0;
    font-size: 4rem;
    position: relative;
    bottom: 0.5rem;
}

.timer .timerbtns button{
margin: 0 1rem
}

/* EggTimer */

.eggtimerapp{
    color: var(--dark);
}

.seteggtimer {
    display: grid;
    grid-template-areas: 'header header'
                         'digits digits'
                         'text   button';
    background-color: var(--primary);
    box-shadow: var(--shadow);
    margin: auto;
    width: 50%;
    /* padding-bottom: 1rem;    */
}

.seteggtimer h2 {
    font-weight: normal;
    grid-area: header;
    padding: 0 2rem;
}
.eggtimerdigits {
    grid-area: digits;
    display: flex;
    justify-content: space-between;
    height: 3rem;
}

.eggtimerdigits div{
    padding: 0 2rem;
}

.seteggtimer .btncontainer {
grid-area: button;
margin: 0 0 1rem 0;
width: 100%;
}

.seteggtimer .btn {
    width: 91%;
    margin-right: 3.8rem;
}

.seteggtimer .btn {
    /* grid-area: button;
    margin: 0 2rem 1rem 0; */
    /* position: relative;
    right: 2rem;
    bottom: 0.75rem; */
}

.eggtimertext{
    grid-area: text;
    /* display: inline; */
    padding: 0 2rem;
    margin-top: 0.4rem;
}

.eggtimertext input {
    margin-left: 0.8rem;
    width: 53.5%;
}

.eggtimerapp ul {
padding: 0;
}

.eggtimer {
    min-height: 5rem;
}

@keyframes alarm {
    0% {background-color: var(--primary);}
    50% {background-color: var(--alarm);}
    100% {background-color: var(--primary);}
}

@-webkit-keyframes alarm {
    0% {background-color: var(--primary);}
    50% {background-color: var(--alarm);}
    100% {background-color: var(--primary);}
}
.alarmeggtimer {
    min-height: 5rem;
    animation-name: alarm;
    animation-iteration-count: infinite;
    animation-duration: 1s;
    -webkit-animation-name: alarm;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-duration: 1s;
}

.eggtimerapp li{
    list-style-type: none;
    background-color: var(--primary);
    box-shadow: var(--shadow);
    margin: auto auto 1rem auto;
    width: 50%;
    
}

.eggtimerform {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1.1rem;
}

.eggtimerform p {
    font-size: 1.4rem;
    text-align: center;
    margin: 0.5rem;
    width: 50%;
}

.btncontainer{
    width: 35%;
    margin-left: 6.3%;
}

.eggtimerform .btn{
    width: 100%;
}

/* Pomodoro */

.setpomodoro {
    display: grid;
    grid-template-areas: 'header header'
                         'dials   dials'
                         'button   button';
    background-color: var(--primary);
    box-shadow: var(--shadow);
    margin: auto;
    width: 50%;
}

.setpomodoro h2 {
    font-weight: normal;
    grid-area: header;
    padding: 1rem 0 0 1rem;
    margin: 0;
}

.pomodorodialscontainer {
    grid-area: dials;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.setpomodoro p {
    text-decoration: underline;
}

.setpomodoro .btn {
    margin: 1rem;
    grid-area: button;
    width: 20%;
    justify-self: center;
}

.pomodorodials{
    padding: 1rem;
    display: grid;
    grid-template-areas: 'para   para' 
                         'label input';
}

.pomodorodials div {
    display: flex;
    flex-direction: column;
}

.pomodorodials p {
    grid-area: para;
}

.pomodorodialslabels{
    grid-area: label;
}

.pomodorodialsinputs{
    grid-area: input;
    margin-right: 6rem;
}

.pomodorotimer {
    display: flex;
    flex-direction: column;
    background-color: var(--primary);
    box-shadow: var(--shadow);
    width: 50%;
    margin: auto;

    overflow: hidden;
    position: relative;
}

.pomodorotimer::after {
    color: rgb(209, 209, 209);
    content: attr(data-bg-text);
    /* display: block; */
    font-size: 18rem;
    /* line-height: 1; */
    position: absolute;
    left: 6rem;
    bottom: -5rem;

}

.pomodorotimer h2 {
    margin-left: 2rem;
    z-index: 1;
}
.pomodorotimer form {
    display: flex;
    flex-direction: column;
    z-index: 1;
}

.pomodorotimer form .btn{
    margin-bottom: 2rem;
    justify-self: center;
    align-self: center;
}
.pomodorotimer form p {
    justify-self: center;
    align-self: center;
    margin: 0 0 1.2rem 0;
    font-size: 4rem;
}

.pomodorobgtext {
    background-color: #aaa;
    overflow: hidden;
    padding: 20px 20px 100px 20px;
    position: relative;
    width: 400px;
}